/* VARIABLES */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;600&display=swap');

:root {
    --black: #171717;
    --red: #DA0037;
    --grey: #444444;
    --white: #EDEDED;
}

/* src/elements.css */
body {
    font-family: 'Montserrat', sans-serif;
    background-color: var(--white);
}

/* TOP NAV */
.topnav {
    overflow: hidden;
    background-color: var(--gray);
}

.topnav a {
    float: left;
    display: block;
    color: var(--white);
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 20px;
}

.topnav a:hover {
    background-color: var(--white);
    color: var(--black);
}

.topnav a.active {
    background-color: var(--red);
    color: var(--white);
}

.topnav .icon {
    display: none;
}

@media screen and (max-width: 600px) {
    .topnav a:not(:first-child) {
        display: none;
    }

    .topnav a.icon {
        float: right;
        display: block;
    }

    .topnav.responsive {
        position: relative;
    }

    .topnav.responsive .icon {
        position: absolute;
        right: 0;
        top: 0;
    }

    .topnav.responsive a {
        float: none;
        display: block;
        text-align: left;
    }
}